.agentUpgradeToFLP {
    &__headingWrapper {
      margin-bottom: 16px;
    }
  
    &__content {
      padding: 24px 20px;
      background: #FFF;
    }
  
    &__title {
      display: inline-block;
      font-size: 18px;
      font-weight: 500;
      margin-top: 0;
      margin-bottom: 0;
      color: #000000;
      margin-right: 16px;
    }
  
    &__status {
      font-size: 12px;
      color: rgba(0, 0, 0, 0.48);
    }
  
    &__type {
      display: inline-block;
      font-size: 8px;
      letter-spacing: 0.08em;
      text-transform: uppercase;
      color: #F2994A;
      margin-left: 4px;
      background: #F0E6C0;
      padding: 2px 4px;
      border: 1px solid #F2994A;
      border-radius: 2px;
    }
  
    &__submit {
      display: flex;
      justify-content: flex-end;
  
      @media (max-width: 992px) {
        margin-top: 20px;
      }
    }
  
    &__info {
      max-width: 476px;
      color: rgba(0, 0, 0, 0.48);
      font-size: 12px;
    }
  }
  