.landingCalendar {
    padding-top: 52px;
    padding-bottom: 52px;
    background-color: #FFF;
    position: relative;
  
    @media (max-width: 1023px) {
      background: #FFF;
    }
  
    &::before {
      position: absolute;
      content: '';
      top: 160px;
      left: -19px;
      background: linear-gradient(90deg, rgba(242, 201, 76, 0.72) 0%, rgba(242, 153, 74, 0.72) 100%), #F2C94C;
      border-radius: 50%;
      width: 51px;
      height: 51px;
  
      @media (max-width: 1023px) {
        display: none;
      }
    }
  
    &__item {
      display: flex;
      margin-bottom: 32px;
      align-items: center;
  
      @media (max-width: 1023px) {
        flex-direction: column;
        text-align: center;
      }
  
      svg {
        flex-shrink: 0;
      }
  
      &Text {
        font-size: 20px;
        color: #333333;
        font-weight: bold;
        margin-left: 32px;
  
        @media (max-width: 1023px) {
          margin-left: 0;
          padding-left: 8px;
          padding-right: 8px;
          margin-top: 8px;
        }
  
        @media (min-width: 1280px) {
          padding-right: 150px;
        }
      }
  
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  
    &__title {
      padding-bottom: 24px;
  
      @media (max-width: 1023px) {
        text-align: center;
      }
    }
  
    &__feature {
      margin-top: 32px;
  
      &_first {
        margin-top: 0;
      }
    }
  
    &__visual {
      width: 721px;
      border-radius: 4px;
      z-index: 2;
      position: relative;
  
      &Wrapper {
        position: relative;
  
        &::before, &::after {
          position: absolute;
          content: '';
          border-radius: 50%;
          z-index: 1;
        }
  
        &::before {
          width: 146px;
          height: 146px;
          background: linear-gradient(90deg, rgba(242, 201, 76, 0.72) 0%, rgba(242, 153, 74, 0.72) 100%), #F2C94C;
          top: 110px;
          left: 2px;
        }
  
        &::after {
          width: 60px;
          height: 60px;
          left: -55px;
          bottom: 38px;
          background: linear-gradient(270deg, rgba(0, 151, 167, 0.72) 0%, rgba(21, 190, 160, 0.72) 100%), #15BEA0;
        }
  
        @media (max-width: 1023px) {
          &::after {
            bottom: -54px;
            width: 36px;
            height: 36px;
            background: linear-gradient(90deg, rgba(242, 201, 76, 0.72) 0%, rgba(242, 153, 74, 0.72) 100%), #F2C94C;
          }
  
          &::before {
            width: 74px;
            height: 74px;
            background: linear-gradient(270deg, rgba(0, 151, 167, 0.72) 0%, rgba(21, 190, 160, 0.72) 100%), #15BEA0;
            top: -20px;
            left: 221px;
          }
        }
  
        @media (max-width: 1023px) {
          text-align: center;
        }
      }
  
      @media (max-width: 1023px) {
        width: calc(100% + 60px);
        box-shadow: 0px 16px 40px rgba(43, 7, 7, 0.06);
        margin: 0 auto;
        margin-left: -30px;
        margin-bottom: 40px;
      }
  
      @media (min-width: 1024px) {
        width: 540px;
        margin-top: 100px;
        margin-left: 22px;
      }
  
      @media (min-width: 1280px) {
        margin-left: -130px;
        width: 721px;
        margin-top: 0;
      }
  
      @media (min-width: 1440px) {
        width: 775px;
        margin-top: 90px;
        margin-left: -137px;
      }
    }
  }