.objectCharacteristics {
    display: flex;
    flex-wrap: nowrap;
    flex-direction: column;
    word-break: break-word;
  
    &__table {
      @media (min-width: 768px) {
        flex: 1 0 170px;
      }
    }
  
    .objectInfoTable {
      width: 50%;
  
      &__holder {
        flex-wrap: nowrap;
  
        @media (max-width: 768px) {
          width: 100%;
        }
      }
    }
  
    &__address {
      display: flex;
      font-size: 12px;
      line-height: 18px;
      color: #999;
  
      @media (max-width: 767px) {
        margin-bottom: 24px;
      }
    }
  }
  
  .objectInfoTable {
    font-size: 12px;
    display: flex;
  
    &__holder {
      display: flex;
      // flex-wrap wrap
    }
  
    &__headers {
      text-align: right;
      color: rgba(0, 0, 0, 0.64);
      margin-right: 16px;
      align-items: space-between;
    }
  
    &__content {
      font-weight: 500;
    }
  
    &__item {
      min-height: 23px;
      line-height: 12px;
      margin-bottom: 0px !important;
    }
  
    &__firstHeader {
      display: flex;
      justify-content: space-between;
    }
  
    &__alerts {
      display: flex;
      flex-wrap: wrap;
    }
  
    &__alertItem {
      color: #EB5757;
      font-size: 12px;
      margin-bottom: 12px;
      margin-right: 12px;
  
      &:last-child {
        margin-right: 0;
      }
  
      svg {
        width: 9px;
        height: 9px;
        margin-right: 9px;
  
        path {
          fill: #EB5757;
        }
      }
    }
  }
  
  .objectInfoTablePrice {
    width: 100%;
    display: flex;
  
    &__item {
      font-size: 12px;
      height: 28px;
      margin-bottom: 0;
      color: rgba(0, 0, 0, 0.64);
      width: 60px;
      margin-right: 16px;
      text-align: right;
      line-height: 22px;
  
      &_big {
        font-size: 16px;
        font-weight: 600;
        flex-grow: 1;
        text-align: left;
        color: #000;
      }
    }
  
    &__oldPrice {
      position: relative;
      font-size: 12px;
      color: #5C5C5C;
      margin-left: 9px;
  
      &:after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 8px;
        height: 1px;
        background-color: #EF5350;
      }
    }
  }