.areaSegmentsLine {
    display: flex;
    position: relative;
    flex-grow: 1;
    margin-bottom: 28px;
    width: 1px;

    &:after {
        content: '';
        position: absolute;
        border-bottom: 1px solid rgba(0, 0, 0, 0.16);
        bottom: 0;
        left: 0;
        right: 0;
    }
}