.notification{
  background: #FFFFFF;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.16);
  border-radius: 0px 0px 4px 4px;
  padding: 8px 6px;
  position: absolute;
  top: 100%;
  left: 50%;
  transition: all .2s;
  margin-top: 0px;
    &__text{
        color: #454545;
        font-size: 12px;
    }
    &__close{
        cursor: pointer;
        }
    &_closed{
          margin-top: -50px;
        }
}
  
 