
.profileCreate {
	background: #EFF3F7;

	hr {
		border: none;
		border-top: 1px solid rgba(208, 216, 220, 0.48);
		margin-top: 16px;
		margin-bottom: 16px;
	}

	.disabledButton {
		background: #D0D8DC;
		box-shadow: none;
		cursor: auto;
		color: #FFF;
	}

	&__info {
		color: rgba(0, 0, 0, 0.48);
		font-size: 12px;
		max-width: 566px;
	}

	&__header {
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 12px 22px;
		background: #FFF;
	}

	&Header {
		&__image {
			width: 32px;
			height: 32px;
			border-radius: 50%;
			overflow: hidden;
		}
	}

	&__content {
		margin: 22px;
		padding: 33px 22px;
		background: #FFF;

		@media (max-width: 767px) {
			margin: 8px;
			padding: 20px 12px;
		}
	}

	&__buttons {
		margin-top: 32px;
		text-align: right;

		button {
			margin-right: 20px;

			&:last-child {
				margin-right: 0;
			}
		}
	}

	&__mobileTab {
		border-top: 1px solid #F2F2F2;
		padding: 23px 12px;
		font-size: 12px;
		font-weight: 600;
		text-transform: uppercase;
		color: rgba(0, 0, 0, 0.48);
		cursor: pointer;
		.check-icon {
			display: inline-block;
			width: 16px;
			height: 16px;
			background-color: #D0D8DC;
			border-radius: 50%;
			margin-right: 8px;
			position: relative;
			vertical-align: top;
	
			&__active {
				background-color: #0097A7;
	
				&+ span {
					color: #0097A7;
				}
			}
	
			&::after {
				content: '';
				position: absolute;
				width: 7px;
				height: 3px;
				border-left: 2px solid #fff;
				border-bottom: 2px solid #fff;
				top: 5px;
				left: 4px;
				transform: rotate(-45deg);
			}
		}

		&_active {
			margin-bottom: 20px;
		}

		&:last-of-type {
			margin-bottom: 20px;
		}
	}


	&__info{
		max-width: 550px;
		font-size: 12px; 
		color: rgba(0, 0, 0, .48); 
			&__tabs {
				margin-bottom: 21px;
		
				.tabs {
					&__container {
						margin: 0 -22px;
						padding: 0 22px;
					}
		
					&__label {
						font-size: 14px !important;
						text-transform: uppercase !important;
						.check-icon {
							display: inline-block;
							width: 16px;
							height: 16px;
							background-color: #D0D8DC;
							border-radius: 50%;
							margin-right: 8px;
							position: relative;
							vertical-align: top;
					
							&__active {
								background-color: #0097A7;
					
								&+ span {
									color: #0097A7;
								}
							}
					
							&::after {
								content: '';
								position: absolute;
								width: 7px;
								height: 3px;
								border-left: 2px solid #fff;
								border-bottom: 2px solid #fff;
								top: 5px;
								left: 4px;
								transform: rotate(-45deg);
							}
						}
					}
				}
			}
		}
}