
@import '../../styles/variables';

.generalLayout {
    height: 100%;

    &__header {
        position: relative;
    }

    &__container {
        display: flex;
        flex-direction: column;
        width: 100%;
        min-height: 100%;
    }

    &_locked {
        position: fixed;
    }

    &__wrapper {
        display: flex;
        flex-grow: 1;
        overflow: hidden;
    }

    &__nav {
        flex-grow: 0;
        flex-shrink: 0;
        width: 160px;
        transition: margin-left 0.15s ease-out;
        margin-left: -160px;
        background-color: #0097A7;

        &_open {
            margin-left: 0;
        }

        @media (min-width: #{$breakpoints_lg}) {
            margin-left: 0;
            width: 60px;
        }
    }

    &__content {
        box-sizing: border-box;
        padding: 8px;
        width: 100%;

        @media (min-width: #{$breakpoints_md}) {
            padding: 0 20px 20px 20px;
        }

        &Wrapper {
            position: relative;
            flex-grow: 1;
            width: 100%;

            @media (min-width: #{$breakpoints_lg}) {
                width: calc(100% - 60px);
            }

            @media (min-width: #{$breakpoints_xl}) {
                width: calc(100vw - 300px);
            }

            &_isNav {
                @media (max-width: 1023px) {
                    width: 100%;
                    flex-shrink: 0;
                    overflow: hidden;

                    .generalLayout__content {
                        position: absolute;
                    }
                }
            }
        }
    }

    &__widgets {
        width: 200px;
        margin: 20px 20px 20px 0;
    }

    &__lightbox {
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 1;
        overflow-y: auto;
        background-color: rgba(#000, 0.5);
        background: radial-gradient(rgba(#000, 0.25), rgba(#000, 0.75));
    }
}