.tooltip {
    position: relative;
    &__icon {
        &:hover+.tooltip__popupHolder {
            visibility: visible;
        }
    }

    &__popupHolder {
        position: absolute;
        visibility: hidden;
        left: 22px;
        top: 50%;
        pointer-events: none;
        z-index: 1;
    }

    &__popup {
        position: relative;
        // margin-top -50%
        // top 7px
        box-shadow: 0px 5px 16px rgba(40, 56, 49, 0.16);
        border-radius: 2px;
        font-size: 12px;
        width: 168px;
        padding: 8px 16px;
        box-sizing: border-box;
        background-color: #fff;
        word-wrap: break-word;

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            margin-top: -4px;
            left: -4px;
            width: 0;
            height: 0;
            border-top: 4px solid transparent;
            border-right: 4px solid #fff;
            border-bottom: 4px solid transparent;
        }
    }

    &-primary {
        svg {
            circle {
                stroke: #0097A7;
            }

            path {
                fill: #0097A7;
            }
        }
    }
}