.clientRequests {
    background: #fff;
    padding: 12px 20px;
    margin-top: 20px;
    border-radius: 2px;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);

    &__title {
        font-size: 10px;
        color: #999;
        margin-bottom: 6px;
    }

    &__requests {
        display: flex;
        justify-content: space-between;
    }

    &__request {
        text-align: center;
    }

    &__numbers {
        font-size: 14px;
        font-weight: 500;
    }

    &__label {
        font-size: 10px;
        font-weight: normal;
    }
}
