.confirmAgency {
    &__label {
      padding: 8px 12px;
      background: #eff3f7;
      font-size: 12px;
      color: #999999;
      cursor: pointer;
      border: 1px solid transparent;
  
      &_active {
        background: transparent;
        color: #0097a7;
        border-color: #0097a7;
      }
  
      &_first {
        border-radius: 2px 0px 0px 2px;
      }
  
      &_second {
        border-radius: 0 2px 2px 0;
      }
    }
  
    &__save {
      margin-left: 36px;
      font-size: 12px;
      color: #0097a7;
      position: relative;
  
      &::before {
        position: absolute;
        content: '';
        left: -20px;
        width: 6px;
        height: 14px;
        border-bottom: 2px solid #0097a7;
        border-right: 2px solid #0097a7;
        transform: rotate(45deg);
      }
    }
  }
  