* {
    box-sizing: border-box;
  }
  
  .objectInfo {
    width: 100%;
    cursor: auto;
    max-height: 0;
    margin-top: 0;
    margin-bottom: 0;
    border-radius: 0px 0px 2px 2px;
    max-width: calc(100vw - 16px);
    background: #FFF;
  
    &__wrapper {
      position: relative;
      background: #eff3f7;
      padding-bottom: 20px;
  
      &::after {
        content: '';
        top: -70px;
        bottom: 20px;
        left: 0;
        border-top: 1px solid #E9ECEE;
        right: 0;
        position: absolute;
        background: transparent;
        pointer-events: none;
        box-shadow: 0px 4px 24px rgba(40, 56, 49, 0.16);
        z-index: 2;
      }
    }
  
    @media (min-width: 768px) {
      width: auto;
      padding: 18px;
    }
  
    @media (max-width: 767px) {
      position: fixed;
      right: 0;
      background: #E5E5E5;
      left: 0;
      top: 0;
      bottom: 0;
      overflow: auto;
      z-index: 5;
      max-width: 100vw;
    }
  
    overflow: hidden;
  
    &__goBack {
      color: #0097A7;
      background: #FFF;
      font-size: 17px;
      padding: 16px;
      box-sizing: border-box;
      display: flex;
      cursor: pointer;
      justify-content: flex-start;
    }
  
    &__active {
      max-height: 1200px;
    }
  
    &__characteristics {
      display: flex;
      flex-direction: column;
      margin-bottom: 32px;
  
      @media (max-width: 767px) {
        margin-right: 0;
        width: 50%;
      }
    }
  
    &__images, &__text, &__messages {
      margin-right: 18px;
    }
  
    &__content {
      display: block;
      float: left;
      width: 100%;
  
      @media (max-width: 767px) {
        margin: 0 8px 8px 8px;
        padding: 0 12px 12px 12px;
        max-width: calc(100% - 16px);
      }
  
      background: #fff;
      align-content: space-between;
  
      @media (max-width: 767px) {
        flex-direction: column;
      }
    }
  
    &__images {
      margin-right: 24px;
      float: left;
  
      @media (min-width: 1024px) {
        width: 35%;
      }
  
      @media (max-width: 1023px) {
        width: 288px;
        margin-right: 24px;
  
        .bVAnyk {
          margin-right: 0;
        }
  
        .fDPcGx {
          display: none;
        }
      }
  
      @media (max-width: 767px) {
        margin-bottom: 20px;
        margin-right: 0px;
        margin-left: 0px;
        width: 50%;
  
        .fDPcGx {
          display: none;
        }
      }
    }
  
    &__text {
      width: calc(35% - 21px);
      float: left;
      margin-top: -16px;
  
      @media (max-width: 1023px) {
        width: calc(100% - 288px - 24px);
        margin-right: 0;
        float: left;
      }
  
      @media (max-width: 767px) {
        margin-right: 0;
        flex: auto;
        width: 100%;
      }
  
      @media (min-width: 1400px) {
        flex-grow: 1;
      }
    }
  
    &__id {
      display: inline-block;
      font-weight: 600;
      line-height: normal;
      font-size: 10px;
      color: rgba(0, 0, 0, 0.32);
  
      @media (max-width: 767px) {
        position: relative;
        top: 12px;
      }
    }
  
    &__messages {
      margin-top: -16px;
      margin-right: 0;
      display: block;
      min-height: 300px;
      float: left;
      width: calc(30% - 21px);
  
      @media (max-width: 1023px) {
        display: none;
      }
    }
  
    &__svg {
      flex-grow: 0;
      margin-right: 12px;
      flex-shrink: 0;
    }
  
    &Text {
      &__tabContent {
        padding-top: 20px;
        padding-bottom: 20px;
        max-width: 100%;
        font-size: 12px;
      }
    }
  
    &__buttons {
      margin-top: auto;
      display: flex;
      padding: 10px 0 0 0;
      box-sizing: border-box;
      flex-shrink: 0;
  
      @media (max-width: 767px) {
        margin: 24px -12px 0 -12px;
        padding: 12px 12px 0 12px;
        border-top: 1px solid #EFF3F7;
      }
    }
  
    &__button {
      flex-grow: 1;
      flex-shrink: 1;
      margin-right: 10px;
      height: 100%;
      text-transform: uppercase;
  
      @media (max-width: 767px) {
        button {
          height: 38px;
        }
      }
  
      &_last {
        margin-right: 0;
      }
  
      .secondaryButton {
        background-color: #eceff1 !important;
      }
    }
  }
  
  .ownerInfo {
    align-items: center;
  
    @media (max-width: 767px) {
      margin: 8px 8px 0 8px;
      justify-content: space-between;
      padding: 12px 12px 8px 12px;
    }
  
    @media (max-width: 575px) {
      justify-content: flex-start;
    }
  
    background: #fff;
    position: relative;
    display: flex;
    margin-bottom: 8px;
  
    &__description {
      width: calc(35% - 48px);
    }
  
    &__name {
      font-weight: 500;
      font-size: 14px;
      color: #000000;
      margin-right: 16px;
    }
  
    &__phone {
      font-size: 12px;
      color: #000000;
    }
  
    &__role {
      font-size: 12px;
      color: rgba(0, 0, 0, 0.64);
    }
  
    &__photo {
      display: inline-block;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      overflow: hidden;
      margin-right: 8px;
    }
  
    &__source {
      margin-left: 24px;
      font-size: 12px;
      margin-top: 3px;
  
      @media (max-width: 767px) {
        margin-left: 0;
      }
  
      color: #000000;
      opacity: 0.48;
    }
  
    &__close {
      // position absolute
      position: absolute;
      right: 22px;
      top: 22px;
      cursor: pointer;
  
      @media (max-width: 767px) {
        display: none;
      }
  
      svg {
        transform: rotate(180deg);
      }
    }
  }
  
  .objectDescription {
    display: flex;
    line-height: 22px;
  }
  