.chatNav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(208, 216, 220, 0.72);
    padding-right: 22px;

    &__name {
        margin: 0;
    }

    &__tabs {
        width: 241px;
        display: flex;
    }

    &__tabItem {
        width: 71px;
        flex-shrink: 0;
        border-right: 1px solid #D0D8DC;
        box-sizing: border-box;
        display: flex;
        padding: 25px 21px 22px 21px;
        flex-wrap: nowrap;
        transition: all .3s;
        overflow: hidden;
        align-items: center;
        border-bottom: 3px solid transparent;
        cursor: pointer;

        &-active {
            width: 170px;
            flex-grow: 1;
            border-bottom-color: #0097A7;

            .chatNav__tabName {
                opacity: 1;
            }
        }
    }

    &__tabName {
        margin: 0;
        text-transform: uppercase;
        font-size: 12px;
        color: #333;
        opacity: 0;
        transition: opacity .3s;
    }

    &__tabIcon {
        margin-right: 16px;
    }
}