@import '../../../styles/variables';
.authFormBox {
    &__form {
      &Block {
        padding: 12px;
        margin-top: 32px;
        border-radius: 2px;
        background: #fff;
  
        @media (min-width: #{$breakpoints_md}) {
          padding: 20px;
        }
      }
  
      &Item {
        margin-top: 6px;
        margin-bottom: 6px;
  
        &_first {
          margin-top: 0;
        }
      }
  
      &Submit {
        margin-top: 24px;
        box-shadow: none;
        width: 280px;
        height: 64px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        margin-right: auto;
        background: linear-gradient(270deg, rgba(0, 151, 167, 0.72) 0%, rgba(21, 190, 160, 0.72) 100%), #15BEA0;
  
        &.disabledButton {
          background: linear-gradient(0deg, #D0D8DC, #D0D8DC);
        }
      }
    }
  
    &__title {
      margin-bottom: 32px;
    }
  
    &__link {
      text-align: center;
    }
  
    &__signin {
      text-align: center;
      color: #15bea0;
      font-weight: 600;
      display: block;
      font-size: 20px;
      margin-top: 20px;
    }
  }