.editLineContacts {
    display: flex;
    align-items: center;
    margin-bottom: 24px;
    position: relative;
    min-height: 51px;

    &__icons {
        display: flex;
        align-items: center;
    }

    &__icon {
        width: 24px;
        height: 24px;
        margin-right: 7px;
        text-align: center;
        line-height: 28px;
        box-sizing: border-box;
        border-radius: 2px;
        cursor: pointer;

        svg {
            width: 15px;
            height: 15px;
            padding: 0 !important;
        }
    }

    &__delete {
        margin: 0 17px 0 0;
    }

    &__add {
        &_active {
            svg {
                path {
                    fill: #0097a7;
                }
            }
        }

        path {
            fill: #D0D8DC;
        }
    }

    &__removeSearch {
        position: absolute;
        top: 25px;
        right: 5px;
        cursor: pointer;
    }

    &__notFound {
        &Suggestion {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            font-size: 12px;
        }

        &AddButton {
            color: #0096a6;
            font-size: 10px;
            text-transform: uppercase;
            display: flex;
            align-items: center;
            font-weight: bold;

            svg {
                margin-right: 8px;
                padding: 0 !important;
                margin-top: -2px;

                path {
                    fill: #0096a6;
                }
            }
        }
    }

    .autoComplete {
        &__item {
            padding: 0 16px;
            height: max-content;
            min-height: 48px;

            &>div {
                width: 100%;
            }
        }

        &__suggestionsContainerOpen {
            margin-top: 0;
        }

        .smallPersonBlock {
            width: 100%;
            border: none;
            margin: 0;
            padding: 8px 0;
        }

        &__content {
            height: max-content;
        }

        &__firstline,
        &__phone {
            line-height: 16px;
        }
    }

    .pureTextField {
        width: 250px;
    }

    .smallPersonBlock {
        margin-bottom: 0;
        margin-right: 20px;
        width: 207px;
    }

}
