.profileTariff {
    width: 200px;
    margin-right: 5px;
    background: #ffffff;
    text-align: center;
    display: flex;
    flex-direction: column;
  
    &__content {
      position: relative;
      padding: 15px 10px;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      box-sizing: border-box;
      background: #ffffff;
      transition: background 0.15s;
      margin-bottom: 45px;
  
      &:hover {
        background: rgba(239, 243, 247, 0.48);
      }
  
      &::after {
        position: absolute;
        content: '';
        bottom: -15px;
        left: -31px;
        z-index: 2;
        right: -31px;
        height: 1px;
        background: #EBE5DD;
      }
  
      &::before {
        position: absolute;
        content: '';
        left: 50%;
        z-index: 3;
        transform: translateX(-50%);
        bottom: -15px;
        height: 2px;
        background: red;
        width: 35%;
      }
    }
  
    &__title {
      margin-top: 10px;
      margin-bottom: 8px;
      font-size: 22px;
      font-weight: 500;
      color: #15bea0;
    }
  
    &__subtitle {
      color: #a5afa9;
      font-size: 12px;
      margin-bottom: 22px;
      margin-top: 0;
    }
  
    &__description {
      font-size: 12px;
      color: #333333;
  
      &_select {
        margin-top: -8px;
        margin-left: -10px;
        margin-right: -10px;
      }
    }
  
    &__forever {
      padding-top: 18px;
      font-size: 32px;
      font-weight: 300;
      color: #333333;
    }
  
    &__additionalCodeInfo {
      font-size: 12px;
      color: #333333;
      margin-top: 8px;
      margin-bottom: 24px;
    }
  
    &__free {
      font-weight: 500;
      color: #15BEA0;
    }
  
    &__fullDescription {
      font-size: 12px;
      color: #15bea0;
      cursor: pointer;
    }
  
    &__bottom {
      margin-top: auto;
    }
  
    &__isActive {
      font-size: 14px;
      font-weight: 500;
      color: #15bea0;
      display: inline-block;
      padding-bottom: 14px;
      position: relative;
  
      &::after {
        position: absolute;
        content: '';
        background: transparent;
        left: -18px;
        top: -2px;
        width: 7px;
        height: 16px;
        border-right: 2px solid #15bea0;
        border-bottom: 2px solid #15bea0;
        transform: rotate(45deg);
      }
    }
  
    &__discount {
      font-weight: 600;
      margin-left: 4px;
      font-size: 12px;
      color: #15BEA0;
    }
  
    &__tariffPrice {
      font-size: 14px;
      color: #333333;
    }
  
    &__order {
      margin-bottom: 8px;
    }
  
    .pureTextField {
      margin-top: -2px;
    }
  
    .select__container > div {
      margin-top: 0;
    }
  
    .select__underline > div {
      text-overflow: unset;
    }
  
    input {
      text-align: center;
    }
  }
  
  .profileTariffOffers {
    &__item {
      margin-bottom: 16px;
      display: flex;
      justify-content: space-between;
    }
  
    &__name {
      font-size: 12px;
      color: #121212;
  
      &_blocked {
        color: #a5afa9;
      }
    }
  
    &__question {
      padding: 0 4px;
      border-radius: 50%;
      border: 1px solid #D0D8DC;
      color: #d0d8dc;
      font-weight: 500;
    }
  }
  