.agencyProfileInfo {
    padding: 20px;
    background-color: #fff;
    box-shadow: 0px 1px 1px rgba(12, 77, 84, 0.06);
    margin-top: 20px;
    position: relative;
  
    &__title {
      font-size: 22px;
      font-weight: 500;
      margin: 0 0 9px 0;
    }
  
    &__status {
      font-size: 10px;
      font-weight: 500;
      color: #EB5757;
      margin-bottom: 4px;
      display: flex;
      align-items: center;
  
      &_verified {
        color: #0097A7;
      }
  
      svg {
        margin-right: 4px;
      }
    }
  
    &__label {
      font-size: 10px;
      color: #999;
    }
  
    &__editIcon {
      position: absolute;
      top: 16px;
      right: 16px;
      cursor: pointer;
    }
  
    &__extendInfoButton {
      display: flex;
      flex-direction: column;
      width: max-content;
      margin: 0 auto;
      align-items: center;
      color: #0097A7;
      font-size: 14px;
      cursor: pointer;
  
      &_reverse {
        flex-direction: column-reverse;
  
        svg {
          transform: rotate(180deg);
          margin-bottom: 5px;
        }
      }
  
      svg {
        margin-top: 5px;
  
        path {
          fill: #0097A7;
        }
      }
    }
  
    .onePhotoHolder {
      width: 90px;
      height: 90px;
    }
  
    button {
      width: 100%;
    }
  
    hr {
      border: none;
      border-bottom: 1px solid rgba(208, 216, 220, 0.72);
    }
  
    div.officeInfo {
      border-top: 1px solid rgba(208, 216, 220, 0.72);
      margin: 0 -20px;
      padding: 10px;
  
      &:first-of-type {
        border: none;
      }
    }
  }