.agentSidebar {
    position: relative;
  
    &__editLink {
      position: absolute;
      padding: 10px;
      right: 2px;
      top: 2px;
    }
  
    &__content {
      background: #FFF;
      padding: 4px 20px 16px 20px;
    }
  
    &__name {
      font-size: 14px;
      margin: 0;
      line-height: 18px;
      color: #000000;
      font-weight: 500;
      margin-bottom: 8px;
    }
  
    &__date {
      font-size: 10px;
      font-weight: 500;
      color: #000000;
      margin-bottom: 6px;
    }
  
    &__status, &__id {
      color: #999999;
      font-size: 10px;
      margin-bottom: 6px;
      font-weight: 500;
    }
  
    &__id {
      margin-bottom: 16px;
    }
  
    &__type {
      color: red;
      margin-top: 6px;
    }
  
    &__arrowDown {
      width: 36px;
  
      svg {
        fill: #000000;
      }
    }
  
    &__message {
      flex-grow: 1;
      margin-right: 8px;
      margin-bottom: 16px;
    }
  
    &__phone, &__link {
      font-size: 12px;
      color: #000000;
    }
  
    &__phone {
      display: flex;
      align-items: center;
  
      svg rect {
        stroke: transparent;
      }
    }
  
    &__link {
      margin-left: 4px;
      margin-bottom: 2px;
  
      &Svg {
        svg {
          width: 13px;
          height: 13px;
          fill: #D0D8DC;
        }
      }
    }
  
    &__addresses {
      font-size: 12px;
      color: #000000;
      margin-top: 8px;
    }
  
    &__requestObject {
      margin-left: 8px;
      font-size: 12px;
      margin-top: 4px;
      color: #000000;
    }
  }
  
  .agentSidebarBalance {
    border-radius: 2px;
    margin-top: 16px;
    margin-bottom: 16px;
  
    &__title {
      padding: 4px 8px;
      color: #ffffff;
      background: #0097a7;
      border-radius: 2px 2px 0 0;
    }
  
    &__content {
      border-radius: 2px;
      border: 1px solid #0097a7;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 8px 12px;
      background: #ffffff;
      color: #0097a7;
    }
  
    &__price {
      font-size: 16px;
      font-weight: 500;
    }
  
    &__plan {
      font-size: 10px;
      font-weight: 500;
    }
  }
  