.onePhotoHolder {
    position: relative;
    width: 152px;
    height: 152px;
    border-radius: 2px;
    overflow: hidden;
    margin-right: 20px;
    margin-bottom: 20px;
  
    &__photo {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background-size: cover;
      background-position: center;
    }
  }