.newRequestPhotos {
    margin-top: 21px;

    &__list {
        display: flex;
        list-style-type: none;
        padding: 0;
        margin: 0;
        flex-wrap: wrap;

        li {
            position: relative;
            margin-top: 20px;

            &:hover {
                .newRequestPhotoItem_realPhoto~.photoHead {
                    opacity: 1;
                }
            }

            .photoHead {
                position: absolute;
                top: 0;
                left: 0;
                right: 22px;
                height: 35px;
                background: linear-gradient(#404040, transparent);
                opacity: 0;
                transition: opacity .2s;

                @media (max-width: 1023px) {
                    opacity: 1;
                }

                &__close {
                    position: absolute;
                    top: 5px;
                    right: 6px;
                    width: 14px;
                    height: 14px;
                    cursor: pointer;

                    &:before,
                    &:after {
                        position: absolute;
                        content: '';
                        width: 18px;
                        height: 2px;
                        top: 6px;
                        right: -2px;
                        background-color: #fff;
                        transform: rotate(45deg);
                    }

                    &:after {
                        transform: rotate(-45deg);
                    }
                }
            }
        }

        .newRequestPhotoItem {
            width: 152px;
            margin-right: 22px;

            &__photoHolder {
                position: relative;
                width: 152px;
                height: 152px;
                box-sizing: border-box;

                &_empty {
                    border: 1px dashed rgba(0, 0, 0, 0.24);
                }
            }

            &__photo {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-size: cover;
                background-position: center;
                border-radius: 2px;
            }

            &__tag {
                font-size: 14px;
                margin-right: 22px;
                height: 32px;
                line-height: 32px;
                margin-top: 6px;

                &>div {
                    width: 100%;
                }
            }

            &__loader {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background: rgba(0, 0, 0, 0.45);
                color: #fff;
            }
        }
    }

    &__youtubeLink {
        display: flex;
        margin-top: 30px;
        margin-bottom: 10px;

        .textField {
            flex-grow: 1;
        }
    }

    .youtubeList {
        display: flex;
        flex-wrap: wrap;
        margin: 30px 0 0 0;
        padding: 0;
        list-style-type: none;

        &__video {
            position: relative;
            width: 152px;
            height: 152px;
            margin-right: 22px;

            &:hover {
                .photoHead {
                    opacity: 1;
                }
            }

            .photoHead {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                height: 35px;
                background: linear-gradient(#404040, transparent);
                opacity: 0;
                transition: opacity .2s;

                &__close {
                    position: absolute;
                    top: 5px;
                    right: 6px;
                    width: 14px;
                    height: 14px;
                    cursor: pointer;

                    &:before,
                    &:after {
                        position: absolute;
                        content: '';
                        width: 18px;
                        height: 2px;
                        top: 6px;
                        right: -2px;
                        background-color: #fff;
                        transform: rotate(45deg);
                    }

                    &:after {
                        transform: rotate(-45deg);
                    }
                }
            }
        }

        &__videoInner {
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            background-size: cover;
            background-position: center;
        }

    }


    &__addVideo {
        width: 24px;
        height: 24px;
        border-radius: 50%;
        background-color: #0097A7;
        position: relative;
        cursor: pointer;

        &:before,
        &:after {
            content: '';
            position: absolute;
            width: 14px;
            height: 2px;
            top: 50%;
            left: 50%;
            margin-left: -7px;
            margin-top: -1px;
            background-color: rgba(255, 255, 255, 0.54);
        }

        &:after {
            transform: rotate(90deg);
        }
    }

    &__tabletDropzone {
        .newRequestDropzone {
            height: 56px;
            width: 100%;

            &__inner {
                height: calc(100% - 16px);
                width: calc(100% - 16px);
                font-size: 10px;
                text-transform: uppercase;
                color: #0097A7;
                font-weight: bold;
            }
        }
    }

}