.header {
	display: flex;
	position: relative;
	z-index: 2;
	background: #fff;
    box-shadow: 0 0 4px rgba(0,0,0,0.15);
    &__item {
        display: flex;
        flex-grow: 1;
        justify-content: center;
        position: relative;
        align-items: center;
        padding: 12px;
        &:hover {
            background-color: rgba(0,0,0,0.15);
        }
        &_moderator {
            margin-left: auto;
            margin-right: 16px;
            &:hover {
                background-color: transparent !important;
            }
            &_date {
                font-weight: normal;
                line-height: normal;
                font-size: 24px;
                text-align: right;
                color: #121212;
                margin-right: 32px;
            }
            &_results {
                font-style: normal;
                font-weight: normal;
                line-height: normal;
                font-size: 12px;
                color: rgba(0,0,0,0.48);
                &__block {
                    display: inline-flex;
                    margin-right: 32px;
                    vertical-align: bottom;
                    &:last-child {
                        margin-right: 0;
                    }
                    span {
                        display: inline-block;
                        &.descr {
                            position: relative;
                            top: 10px;
                        }
                        &.big {
                            font-style: normal;
                            font-weight: normal;
                            line-height: normal;
                            font-size: 24px;
                            text-align: right;
                            color: #121212;
                            margin-right: 5px;
                            &.red {
                                color: #eb5757;
                            }
                            &.green {
                                color: #27ae60;
                            }
                        }
                    }
                    
                    
                }
            }
        }
        &:hover {
            background-color: rgba(0,0,0,0.15);
        }
        @media (min-width: 576px) {
            flex-grow: 0;
            &_calendar {
                margin-left: auto;
            }
        }
        @media (min-width: 1280px) {
            &_client {
                margin-left: auto;
            }
        }
    }
    &__logo {
        display: block;
        height: 25px;
    @media (min-width: 1024px) {
        height: 32px;
        }
    }
    &__profile {
        position: relative;
        &Picture {
            display: block;
            width: 32px;
            height: 32px;
            border-radius: 100%;
        }
        &Dropdown {
            position: absolute;
            display: none;
            right: 0;
            top: 100%;
            width: 160px;
            margin-top: 8px;
            background-color: #fff;
            box-shadow: 0 0 12px rgba(0,0,0,0.15);
            box-sizing: border-box;
            border-radius: 2px;
            &_active {
                display: block;
            }
            &:before {
                content: '';
                position: absolute;
                height: 0px;
                border-left: 5px solid transparent;
                top: -6px;
                right: 12px;
                border-right: 6px solid transparent;
                border-bottom: 6px solid #fff;
            }
            &Item {
                text-decoration: none;
                color: #000;
                display: block;
                padding: 12px;
                &:hover {
                    background-color: rgba(0,0,0,0.05);
                }
            }
        }
    }
    &__searchBar {
        margin: 12px 12px 12px auto;
    }
    
    
}

