.objectInfoMessages {
    &__messages {
      display: flex;
      flex-direction: column;
      width: 100%;
      flex-grow: 1;
      min-height: 240px;
  
      @media (min-width: 1024px) {
        height: 100%;
        min-height: 279px;
      }
  
      .pureTextFieldPlane {
        margin-top: auto;
      }
    }
  
    &__input {
      margin-top: auto;
  
      .textField__underline {
        margin-bottom: 0;
      }
    }
  
    &__tabs {
      display: flex;
      height: 100%;
      flex-direction: column;
    }
  }