.investorsPage,
.resumesPage {
    min-height: 100vh;
    padding-top: 36px;
    box-shadow: inset 0px 0px 250px rgba(0, 0, 0, 0.16);
    position: relative;

    .addButton,
    .disabledButton {
        @media (max-width: 1023px) {
            margin-top: 16px;
        }
    }

    .disabledButton {
        background: grey;
        color: #ffffff;
        box-shadow: none;
    }

    @media (max-width: 1023px) {
        padding-top: 8px;
    }
}

.investorsPage {
    overflow: hidden;

    &__content {
        padding-top: 16px;
        background: rgba(197, 242, 228, 0.24);

        @media (max-width: 1023px) {
            padding-left: 20px;
            padding-right: 20px;
        }
    }

    &__possibilities {
        display: flex;
        align-items: center;
        font-size: 20px;
        font-weight: 700;
        line-height: 23px;
        color: #333333;

        &Wrapper {
            border-radius: 4px;
            box-shadow: 0 2px 4px rgba(43, 7, 7, 0.08);
            background: #FFF;
            padding: 32px;
            margin-bottom: 32px;
        }

        @media (max-width: 1023px) {
            flex-direction: column;
            align-items: flex-start;

            &Wrapper {
                padding-left: 12px;
                padding-right: 12px;
            }
        }
    }

    &__number {
        color: #07a4a5;
        font-size: 20px;
        line-height: 29px;
        width: 48px;
        margin-right: 8px;
        height: 48px;
        border-radius: 50%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        background: rgba(185, 233, 227, 0.78);

        &Content {
            opacity: 1;
        }

        @media (max-width: 1023px) {
            margin-bottom: 16px;
        }
    }

    &__principle {
        width: 20%;
        line-height: 20px;
        margin-bottom: 40px;
        display: flex;
        flex-direction: column;
        font-size: 14px;
        opacity: 0.72;
        color: #333333;

        @media (max-width: 1023px) {
            padding-left: 20px;
            padding-right: 20px;
        }

        @media (max-width: 767px) {
            width: 35%;
        }

        @media (max-width: 530px) {
            width: 100%;
            align-items: center;
            text-align: center;
        }

        svg {
            margin-bottom: 24px;
            opacity: 1;
        }

        &Wrapper {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
        }

        &Title {
            margin-bottom: 48px;
            font-weight: 700;
            font-size: 20px;
            width: unset;
            opacity: 1;

            @media (max-width: 1023px) {
                margin-left: auto;
                margin-right: auto;
            }
        }
    }

    &__principles {
        padding-top: 64px;
        background: #FFF;
        position: relative;

        &::before,
        &::after {
            border-radius: 50%;
            position: absolute;
            content: '';

            @media (max-width: 1023px) {
                display: none;
            }
        }

        &::before {
            top: 38px;
            background: linear-gradient(90deg, rgba(242, 201, 76, 0.72) 0%, rgba(242, 153, 74, 0.72) 100%), #F2C94C;
            width: 50px;
            height: 50px;
            left: 60%;
        }

        &::after {
            top: 130px;
            right: 60px;
            width: 26px;
            height: 26px;
            background: linear-gradient(270deg, rgba(0, 151, 167, 0.72) 0%, rgba(21, 190, 160, 0.72) 100%), #15BEA0;
        }
    }

    &__checkList {
        padding-top: 36px;
        padding-bottom: 36px;
        background: rgba(255, 246, 198, 0.6);
        position: relative;

        &::before,
        &::after {
            border-radius: 50%;
            position: absolute;
            content: '';

            @media (max-width: 1023px) {
                display: none;
            }
        }

        @media (max-width: 1023px) {
            padding-left: 20px;
            padding-right: 20px;
        }

        &::before {
            width: 153px;
            height: 153px;
            background: linear-gradient(270deg, rgba(0, 151, 167, 0.72) 0%, rgba(21, 190, 160, 0.72) 100%), #15BEA0;
            left: -121px;
            top: 22px;
        }

        &::after {
            right: 31px;
            width: 50px;
            height: 50px;
            top: 131px;
            background: linear-gradient(90deg, rgba(242, 201, 76, 0.72) 0%, rgba(242, 153, 74, 0.72) 100%), #F2C94C;
        }

        &Wrapper {
            background: #FFF;
            padding: 32px !important;
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            border-radius: 4px;
            box-shadow: 0px 2px 4px rgba(43, 7, 7, 0.08);
            position: relative;

            &::before {
                border-radius: 50%;
                position: absolute;
                content: '';
                width: 54px;
                height: 54px;
                background: linear-gradient(270deg, rgba(0, 151, 167, 0.72) 0%, rgba(21, 190, 160, 0.72) 100%), #15BEA0;
                bottom: 36px;
                right: 100px;
            }

            @media (max-width: 1023px) {
                &::before {
                    display: none;
                }

                padding-left: 12px;
                padding-right: 12px;
            }
        }

        &Title {
            padding-left: 32px;
            width: 100%;
            font-size: 20px;
            flex-shrink: 0;
            margin-bottom: 24px;
            font-weight: bold;
            color: #333333;
            opacity: 0.72;
        }

        &Part {
            width: 42%;

            @media (max-width: 1023px) {
                width: 100%;
            }
        }

        &Item {
            font-size: 14px;
            line-height: 20px;
            color: #333333;
            opacity: 0.72;
            margin-bottom: 16px;
            display: flex;

            svg {
                margin-right: 14px;
                flex-shrink: 0;
                cursor: auto;
            }
        }
    }

    &__exclamation {
        width: 32px;
        flex-shrink: 0;
        height: 32px;
        border-radius: 50%;
        border: 1px solid #27ae60;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 24px;
        font-weight: bold;
        margin-right: 8px;
        color: #27ae60;
    }

    &__title {
        font-size: 32px;
        letter-spacing: 0.03em;
        font-weight: bold;
        margin-top: 0;
        color: #333333;
        margin-bottom: 24px;
        position: relative;

        &::after {
            position: absolute;
            content: '';
            width: 32px;
            height: 32px;
            top: -6px;
            left: 440px;
            border-radius: 50%;
            background: linear-gradient(90deg, rgba(242, 201, 76, 0.72) 0%, rgba(242, 153, 74, 0.72) 100%), #F2C94C;

            @media (max-width: 1023px) {
                display: none;
            }
        }
    }

    &__paragraph {
        font-size: 24px;
        color: #8C8883;
        margin-bottom: 24px;
        display: flex;

        &_small {
            font-size: 14px;
            margin-bottom: 32px;
            line-height: 20px;
            opacity: 0.72;
            max-width: 395px;
        }
    }

    li {
        line-height: 20px;
        font-size: 14px;
        margin-left: -40px;
        padding-right: 40px;
        color: #333333;
        opacity: 0.72;

        &.noNumber {
            list-style-type: none;
        }
    }

    &__list {
        padding-left: 32px;

        li {
            margin-bottom: 4px;
        }

        @media (max-width: 1023px) {
            padding-left: 26px;
        }

        &Title {
            font-weight: bold;
            font-size: 14px;
            line-height: 20px;
            color: #333333;
            opacity: 0.72;
            margin-bottom: 16px;
        }

        &_last {
            padding-left: 0;
            position: relative;

            @media (max-width: 1023px) {
                padding-left: 26px;
                margin-top: -26px;
            }

            &::after,
            &::before {
                content: '';
                position: absolute;
                border-radius: 50%;
            }

            &::after {
                background: linear-gradient(270deg, rgba(0, 151, 167, 0.72) 0%, rgba(21, 190, 160, 0.72) 100%), #15BEA0;
                width: 54px;
                height: 54px;
                left: 400px;
                top: 350px;
            }

            &::before {
                background: linear-gradient(90deg, rgba(242, 201, 76, 0.72) 0%, rgba(242, 153, 74, 0.72) 100%), #F2C94C;
                width: 93px;
                height: 93px;
                left: 410px;
                top: -240px;
            }

            @media (max-width: 1023px) {

                &::after,
                &::before {
                    display: none;
                }
            }
        }
    }

    &__form {
        background: #ffffff;
        box-shadow: 0 16px 32px rgba(7, 43, 24, 0.12);
        border-radius: 4px;
        padding-top: 24px;
        padding-bottom: 24px;

        .textField {
            margin-bottom: 8px;
        }

        .row__col:last-child {
            align-items: center;
            margin-top: -12px;
            display: flex;
        }
    }
}