.weekTable {

    &__header {
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.11);
        position: relative;
        z-index: 1;

        ul {
            display: flex;
            list-style-type: none;
            padding-left: 45px;
            margin: 0;

            .headerDay {
                flex-grow: 1;
                text-align: center;

                &__holder {
                    width: max-content;
                    margin: 12px auto 7px auto;
                }

                &__dow {
                    font-size: 12px;
                    color: #333;
                    text-align: left;
                    text-transform: capitalize;
                }

                &__date {
                    font-size: 44px;
                }

                &--today {
                    background-color: #F7F9FB;

                    .headerDay__date,
                    .headerDay__dow {
                        color: #0097A7;
                    }
                }

                &--previous {

                    .headerDay__date,
                    .headerDay__dow {
                        color: #999;
                    }
                }
            }
        }
    }

    &__contentHolder {
        height: 60vh;
    }

    &__content {
        display: flex;
    }

    &__hours {
        width: 45px;
        flex-shrink: 0;
    }

    &__hourItem {
        height: 72px;
        border-bottom: 1px solid rgba(232, 236, 238, 0.5);
        font-size: 10px;
        padding-top: 8px;
        padding-left: 7px;
        box-sizing: border-box;
    }

    &__day {
        flex-grow: 1;
        flex-basis: 0;
        border-left: 1px solid rgba(232, 236, 238, 0.5);

        &--today {
            background-color: #F7F9FB;

            .calendarWeekTask {
                // opacity: 0.48;
            }
        }
    }

    &__date {
        height: 72px;
        border-bottom: 1px solid rgba(232, 236, 238, 0.5);
        position: relative;
        box-sizing: border-box;
        padding-top: 2px;
    }
}