.modal {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 30;
    background-color: rgba(#000, 0.5);
    background: radial-gradient(rgba(#000, 0.25), rgba(#000, 0.75));

    &__close {
        position: absolute;
        right: 21px;
        top: 21px;
        cursor: pointer;
    }

    &__container {
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: auto;
    }

    &__item {
        position: absolute;
        background: #fff;
        margin: 20px 0;
        box-shadow: 0 0 10px rgba(#000, 0.5);
        box-sizing: border-box;
        width: 400px;
        top: 7vh;
        margin-bottom: 50px;
        border-radius: 2px;

        &--noSetWidth {
            width: max-content;

            @media (max-width: 487px) {
                width: 100%;
            }
        }

        &--centerTop {
            top: unset;
            margin: unset;
        }
    }
}