@import '../../../../../styles/variables';

.landingClientRequestFeatures {
  display: flex;
  flex-wrap: nowrap;
  margin-left: -6px;
  margin-right: -6px;

  @media (min-width: #{$breakpoints_md}) {
    margin-left: -10px;
    margin-right: -10px;
  }

  @media (min-width: #{$breakpoints_lg}) {
    margin-left: 0;
    margin-right: 0;
    display: block;
  }

  &__item {
    display: flex;
    box-sizing: border-box;
    padding-left: 6px;
    flex-wrap: wrap;
    padding-right: 6px;

    @media (max-width: 1023px) {
      max-width: calc(100vw - 12px);
      flex-grow: 0;
      flex-shrink: 0;
      flex-basis: 100%;
    }

    @media (min-width: #{$breakpoints_md}) {
      padding-left: 10px;
      padding-right: 10px;
    }

    &_first {
      @media (min-width: #{$breakpoints_lg}) {
        margin-top: 0;
      }
    }

    @media (min-width: #{$breakpoints_md}) {
      padding-left: 10px;
      padding-right: 10px;
    }

    @media (min-width: #{$breakpoints_lg}) {
      margin-top: 16px;
      padding: 0;
      display: block;
    }
  }

  &__dotNav {
    @media (min-width: #{$breakpoints_lg}) {
      display: none;
    }

    .dotNav {
      width: max-content;
      margin: 0 auto;
      display: flex;
      justify-content: center;

      &__container {
        margin-right: 16px;

        &:last-child {
          margin-right: 0;
        }
      }

      &__item {
        width: 16px;
        height: 16px;
      }
    }
  }
}